import React from 'react';
import '../Styles/App.css';
import {Link} from 'react-router';

export const Header = (props) => {
  let sty = {color:'#eee'}
  let sty1 = {borderBottom:'1px solid #ddd', borderRadius:'5px'}
    return (
      <div className="Header" >
        <nav className="navbar navbar-inverse" style={{backgroundColor:'rgb(54, 54, 129)', border: '0'}}>
          <div className="container-fluid" >
            <div className="navbar-header">
              <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                <span className="icon-bar"></span>
                <span className="icon-bar"></span>
                <span className="icon-bar"></span>                        
              </button>
              <a className="navbar-brand" style={{color:'#eee', fontSize:'25px', marginLeft:'1px'}} href="home">WeConnect *</a>
            </div>
            <div className="collapse navbar-collapse" id="myNavbar" >

              <ul className="nav navbar-nav navbar-right">
                <li><Link to="/home" style={sty} activeStyle={sty1}><span className="glyphicon glyphicon-home"></span> Home</Link></li>
                <li><Link to="/addbusiness" style={sty} activeStyle={sty1}><span className="glyphicon glyphicon-plus-sign"></span> Add Business</Link></li>
                <li><Link to="/signup" style={sty} activeStyle={sty1}><span className="glyphicon glyphicon-user"></span> Sign Up</Link></li>
                <li><Link to="/login" style={sty} activeStyle={sty1}><span className="glyphicon glyphicon-log-in"></span> Login</Link></li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    );
  }

export default Header;


