import React, { Component } from 'react';
import '../Styles/App.css';
import Header from './Header';
import Footer from './Footer';

class CreateBusiness extends Component {
  render() {
    return (
      <div className="row">
      <Header />
      <FormContent />
      <Footer />
      </div>     
    );
  }
}

class FormContent extends Component {
  render() {
    return (
      <div className="businesscontent">
        <div className = "row">
          <h3 style={{paddingLeft:'20px',color:'break'}}>Create a business</h3><br />
          <div className="col-md-1" ></div>
          <div className="col-md-10" >
            
            <form>
              <div className="form-group">
                <label for="email">Name:</label><input type="email" id="field" />
                
              </div><br />
              <div className="form-group">
                <label for="pwd">Logo:</label>
                <input type="text" id="field" />
              </div><br />
              <div className="form-group">
                <label for="pwd">Location:</label>
                <input type="text" id="field"/>
              </div><br />
              <div className="form-group"> 
                <label for="pwd">Contacts:</label>
                <input type="text" id="field" />
              
              </div><br />

              <div class="form-group">
                <label for="pwd">About:</label><br /><br />
                <textarea name="" class="form-control" cols="30" rows="4"> </textarea>
              </div><br />
            <a href="index.html" style={{float:'right'}}><button type="submit" class="btn btn-default">Submit</button></a>
          </form>
              
  
          </div>
          <div className = "col-md-1"></div>
  
        </div>
      </div>
    
          );
        }
      }

export default CreateBusiness;



