import React, { Component } from 'react';
import '../Styles/App.css';
import Header from './Header';
import Footer from './Footer';
import capstone from '../Images/capstone.png';

class Business extends Component {
  render() {
    return (
      <div className="row">
      <Header />
      <BusinessContent />
      <Footer />
      </div>     
    );
  }
}

class BusinessContent extends Component {
  render() {
    return (
      <div className="content" style={{padding:'25px'}}>
        <br />
        <div style={{textAlign:'center'}}>
          <img src={{capstone}} alt="Business Logo" className="img-thumbnail" /><br />
          <span id="btitle">Capstone Kenya</span>

        </div>

        <div>
          <h4 id="details">About</h4>
          <div>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et 
              dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip
                ex ea commodo consequat. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip
                ex ea commodo consequat.
          </div>            
        </div><br />

        <div>
          <h4  id="details">Location</h4>  
          Nairobi - kenya: TRM          
        </div><br />

        <div>
          <h4  id="details">Contacts</h4>  
          +254 721 988 543,+254 799 999 555, capstoneorg@gmail.com         
        </div><br />

        <div>
          <h4  id="details">Reviews <button className="btn btn-primary" onclick="document.getElementById('reviewmodal').style.display='block'" style={{marginLeft:'10%'}}><span className="glyphicon glyphicon-plus-sign"></span> Add Review</button></h4> 
          
          <div id="reviewmodal" className="modal" style={{margin: '15%'}}>

              <form className="modal-content animate" action="account.html" style={{paddingLeft:'20px'}}>


              <button type="button" id="close" onclick="document.getElementById('reviewmodal').style.display='none'" >
                  <span className="glyphicon glyphicon-remove"></span>
              </button>

              <label for="username" ><b>My Review:</b></label><br /><br />
              <textarea cols="30" rows="3"></textarea><br /><br />
              <button type="submit"><a href="business.html">Submit</a></button><br /><br />

              </form>
          </div>
              
          <div className="row">
            <div className="col-md-10" id="reviews" >
                <div id="review">
                    Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu 
                    iat nulla pariatur Velit. <br />
                    <font id="reviewer"> Annetee</font><br />                      
                </div><br />
              </div>
            <div className="col-md-2"></div>
          </div>
        </div>          
      </div>
        
          );
        }
      }

export default Business;



