import React, { Component } from 'react';
import '../Styles/App.css';
import Header from './Header';
import Footer from './Footer';
import {Link} from 'react-router';

class Home extends Component {
  render() {
    return (
      <div className="row">
      <Header />
      <HomeContent />
      <Footer />
      </div>     
    );
  }
}

class HomeContent extends Component {
  render() {
    return (
      <div className="content">
        <br />
    
        <div className="search">
            <span><input type="text" id="search" placeholder="Search.."/></span>
            <span><button type="submit" id="search-icon" ><span className="glyphicon glyphicon-search" ></span></button></span>
        </div><br /><br />
        <div style={{textAlign:'center'}}>
              
        <h3 id="h1">Resourceful, Reliable, Individuals - Business connect platform.</h3>
        <h3>You need it, Here you go, <font style={{color:'rgb(173, 32, 32)'}}>Connect *</font></h3>
        </div>
        
        <div className = "row" style={{padding:'40px'}}>

          <div className = "col-md-8" style={{textAlign:'left'}}>
                <h4 style={{color:'maroon'}} >Featured Businesses</h4><br />
                
            <table className="table">
                    
              <tr>
                <th id="name-row">Business</th>
                <th id="row">Category</th>
                <th id="row">Location</th>
              </tr> <br />
              <tr>
                <td><Link to="/business" >Capstone Kenya</Link></td>
                <td>Technology</td>
                <td>Nairobi</td>
              </tr> <br />
            
              <tr>
                <td><a href="business.html">Kilimall International</a> </td>
                <td>Online Shopping</td>
                <td>Naivasha</td>
              </tr> <br />
                
              <tr>
                  <td><a href="business.html">Alladin Fashion</a> </td>
                  <td>Fashion</td>
                  <td>Kitengela</td>
                </tr> <br />

              <tr>
                  <td><a href="business.html">Jade Collection</a> </td>
                  <td>Fashion</td>
                  <td>Samburu</td>
                </tr> <br />
            </table>
          </div>

          <div className = "col-md-1"></div>

          <div className = "col-md-3" style={{textAlign:'left'}}>

            <h4 >Filter Businesses By:</h4><br />

            <span>Category: </span><span>          
            <select name="" id="filter">
              <option value="">Default</option>
              <option value="">Technology</option>
              <option value="">Online Shopping</option>
              <option value="">Fashion</option>
              <option value="">Hotel</option>
              <option value="">Building</option>
            </select></span><br /><br />

            <span>Location: </span><span>          
              <select name="" id="filter">
                <option value="">Default</option>
                <option value="">Nairobi</option>
                <option value="">Naivasha</option>
            </select></span>

          </div>
          


        </div>

        
      </div>
          );
        }
      }

export default Home;



