import React from 'react';
import '../Styles/App.css';
import {Link} from 'react-router';

export const Footer = (props) => {
  let sty = {color:'#eee'}
    return (
      <div className="Footer" >
      <br />
        <nav class="navbar navbar-inverse" style={{backgroundColor:'rgb(54, 54, 129)', border: '0'}}>
          <div class="container-fluid" >
            <div class="navbar-header">
              <button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>
                <span class="icon-bar"></span>                        
              </button>
              
            </div>
            <div class="collapse navbar-collapse" id="myNavbar" >

              <ul class="nav navbar-nav navbar-right"><li><Link to="/home" style={sty}><span class="glyphicon glyphicon-question-sign"></span> Help</Link></li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    );
  }

export default Footer;


