import React, { Component } from 'react';
import '../Styles/App.css';
import Header from './Header';
import Footer from './Footer';

class Signup extends Component {
  render() {
    return (
      <div className="row">
      <Header />
      <SignupContent />
      <Footer />
      </div>     
    );
  }
}

class SignupContent extends Component {
  render() {
    return (
      <div className="signupcontent">
        <div className = "row">
          <h3 style={{paddingLeft:'20px',color:'break'}}>Signup</h3><br />
          <div className="col-md-1" ></div>
          <div className="col-md-10" >
            
            <form>
              <div className="form-group">
                <label for="email">Username:</label><input type="email" id="field" />
                
              </div><br />
              <div className="form-group">
                <label for="pwd">Email:</label>
                <input type="text" id="field" />
              </div><br />
              <div className="form-group">
                <label for="pwd">Password:</label>
                <input type="password" id="field"/>
              </div><br />
              <div className="form-group"> 
                <label for="pwd">Confirm pwd:</label>
                <input type="password" id="field" />
              
              </div><br />
            <a href="index.html" style={{float:'right'}}><button type="submit" class="btn btn-default">Submit</button></a>
          </form>
              
  
          </div>
          <div className = "col-md-1"></div>
  
        </div>
      </div>
    
          );
        }
      }

export default Signup;



